import React from 'react';
import axios from 'axios';

const getAllRecord = () => axios({
    url: process.env.REACT_APP_APISERVER + "/record/list",
    method: "get"
  })
  .then(res => {
    if (res.data.length === 0)
        return "üö©Ï°¥Ïû¨ÌïòÏßÄ ÏïäÏäµÎãàÎã§.";
    else 
        return res.data.map(json => <li key={json.nickName}>
          {
            json.nickName + " " + 
            json.time + "Ï¥à" + 
            json.width + " " + 
            json.height + " " +
            json.minePercent 
          } </li>); 
  })
  .catch(error => {
    return "Ïó∞Í≤∞Ïù¥ Î∂àÏïàÏ†ïÌï©ÎãàÎã§.";
  });

export default getAllRecord;